<template>
    <v-row>
        <v-col>
            <v-card>
                <v-card-text>
                    <h6 class="text-h6 font-weight-bold">
                        {{ survey.title }} <br />
                        <small><span class="text-red">From: {{ moment(survey.startDate).format("D-MMM-yyyy") }} To: {{
                            moment(survey.endDate).format("D-MMM-yyyy") }}</span></small>
                    </h6>
                </v-card-text>
            </v-card>
            <v-card class="my-2">
                <v-card-text>
                    <table class="tbl-report">
                        <thead>
                            <tr>
                                <th>Its Id</th>
                                <th>Full Name</th>
                                <th>Team</th>
                                <th>Section</th>
                                <th>Rank</th>
                                <th v-for="question in survey.questions" :key="question.questionId">{{ question.reportHeader }}</th>
                            </tr>
                        </thead>
                        <tbody>
                            <tr v-for="rec in report" :key="rec.ItsId">
                                <td>{{ rec.ItsId }}</td>
                                <td>{{ rec.FullName }}</td>
                                <td>{{ rec.Team }}</td>
                                <td>{{ rec.Section }}</td>
                                <td>{{ rec.Rank }}</td>
                                <td v-for="question in survey.questions" :key="question.questionId">
                                    <template v-if="question.questionType == 'FileUpload'">
                                        <template v-if="isImageUrl(rec[question.reportHeader].split(' ')[0])">
                                            <ImageZoom :src="img" v-for="(img, i) in rec[question.reportHeader].split(' ')"
                                                :key="`img_${i}`" />
                                        </template>
                                        <template v-else>
                                            <a :href="url" target="_blank"
                                                v-for="(url, i) in rec[question.reportHeader].split(' ')" :key="`a_${i}`"
                                                style="display:block;">View File</a>
                                        </template>
                                    </template>
                                    <template v-else>
                                        {{ rec[question.reportHeader] }}
                                    </template>
                                </td>
                            </tr>
                        </tbody>
                    </table>
                </v-card-text>
            </v-card>
        </v-col>
    </v-row>
</template>

<script setup>
import { ref, inject, onMounted } from 'vue';
import { useRoute } from 'vue-router';
import moment from 'moment';
import * as surveyService from '@/services/surveyService';
import ImageZoom from '@/components/imageZoom.vue';

const loader = inject("loader");
const snackbar = inject("snackBar");
const route = useRoute();

const surveyId = route.params.id;
var survey = ref({});
var report = ref([]);

function isImageUrl(url) {
    return /\.(jpg|jpeg|png|gif|bmp|webp|svg)$/i.test(url);
}

async function getData() {
    loader.show();

    let result = await surveyService.getReport(surveyId)
        .catch(error => {
            console.log(error);
            snackbar.error(error);
        })
        .finally(async () => {
            loader.hide();
        });

    if (result) {
        survey.value = result.survey;
        report.value = result.report;
    }
}

onMounted(async () => {
    await getData();
})

</script>

<style>
.tbl-report {
    width: 100%;
    border-collapse: collapse;
    border-radius: 10px;
    /* border: 1px Solid #666; */
}

.tbl-report td {
    border-bottom: 1px Solid #666;
}
</style>