<template>
    <v-row v-if="'description' in event">
        <v-col>
            <v-card>
                <v-card-text>
                    <h6 class="text-h6 font-weight-bold">
                        {{ event.description }} - {{ moment(event.dutyDate).format("D-MMM-yyyy") }}<br />
                        <small><span class="text-red">
                                Reporting Time: {{ moment(event.reportingTime).format("hh:mm A") }}
                            </span></small><br />
                        <small>
                            <span class="text-red">
                                Scanning from: {{ moment(event.scanningStart).format("hh:mm A") }}
                                to {{ moment(event.scanningEnd).format("hh:mm A") }}
                            </span>
                        </small>
                    </h6>
                    <!-- <router-link to="/attendance">Back to Event</router-link> -->
                </v-card-text>
                <v-card-text align="end">
                    <v-btn @click="showReportDialog">Download Report</v-btn>
                </v-card-text>
            </v-card>
            <v-card class="mt-3">
                <v-card-text>
                    <v-list density="compact" v-if="attendance.length > 0">
                        <v-list-item density="compact" v-for="(att, ix) in attendance" :key="att.member.itsId"
                            :title="`${ix + 1} - ${att.member.itsId} - ${att.member.fullName} - ${att.member.rank} (${att.member.organization})`"
                            :subtitle="formatTime(att.reportingTime)" :prepend-avatar="att.member.pictureInUniform">
                        </v-list-item>
                    </v-list>
                    <h4 v-else class="text-center">No Attendance Marked</h4>
                </v-card-text>
            </v-card>
            <v-card class="mt-3">
                <v-card-text>
                    <v-text-field v-model="selectedMemberId" label="Enter Its Number" @keydown.enter="addMember"
                        hide-details />
                </v-card-text>
            </v-card>
        </v-col>
    </v-row>

    <simple-dialog ref="dlgReport" width="450px" confirmButtonText="Download" @ok="downloadReport">
        <template v-slot:content>
            <VueDatePicker v-model="reportParams.startDate" time-picker-inline disable-year-select
                            :enable-time-picker="false" auto-apply format="dd-MMM-yyyy"
                            :text-input="{ format: 'dd-MMM-yyyy' }" model-type="dd-MMM-yyyy" placeholder="dd-MMM-yyyy" />

            <VueDatePicker v-model="reportParams.endDate" time-picker-inline disable-year-select
                            :enable-time-picker="false" auto-apply format="dd-MMM-yyyy"
                            :text-input="{ format: 'dd-MMM-yyyy' }" model-type="dd-MMM-yyyy" placeholder="dd-MMM-yyyy" class="mt-3" />
        </template>
    </simple-dialog>
</template>

<script setup>
import { ref, inject, onMounted } from 'vue';
import { useRoute } from "vue-router";
import * as atService from '@/services/attendanceService';
import moment from 'moment';

const loader = inject("loader");
const snackbar = inject("snackBar");
const router = useRoute();

const attendanceId = router.params.id;
const event = ref({});
const attendance = ref([]);
const selectedMemberId = ref("");
const dlgReport = ref(null);

const reportParams = ref({
    startDate: new Date(),
    endDate: new Date(),
});

function formatTime(dt) {
    return moment(dt).format("DD-MMM-YYYY hh:mm A");
}

async function getData() {
    loader.show();

    let result = await atService.master_getById(attendanceId)
        .catch(error => console.log(error))
        .finally(async () => {
            await getDetail();
            loader.hide();
        });

    event.value = result;
}

async function getDetail() {
    let result = await atService.detail_getAllByMasterId(attendanceId)
        .catch(error => console.log(error))

    if (result)
        attendance.value = result;
}

async function addMember() {
    if (selectedMemberId.value == null) {
        snackbar.error("Please enter an Its number");
        return;
    }

    loader.show();

    await atService.detail_add(attendanceId, { itsId: selectedMemberId.value })
        .then(async () => {
            snackbar.success("Attendance has been marked successfully")
            await getDetail();
        })
        .catch(error => {
            snackbar.error(error);
            console.log(error);
        })
        .finally(() => {
            loader.hide();
        });

    selectedMemberId.value = null;
}

async function showReportDialog() {
    dlgReport.value.show();
}

async function downloadReport() {
    console.log(reportParams.value);

    loader.show();
    
    const result = await atService.report_detail(attendanceId, reportParams.value)
    .catch(ex => snackbar.error(ex));
    
    loader.hide();
    
    // Create a URL for the file blob
    const url = window.URL.createObjectURL(new Blob([result]));
    const link = document.createElement('a');
    link.href = url;
    link.setAttribute('download', `attendance_${attendanceId}.xlsx`); // Replace with your desired file name
    document.body.appendChild(link);
    link.click();

    // Clean up
    window.URL.revokeObjectURL(url);
    document.body.removeChild(link);
}

onMounted(async () => {
    await getData();

    console.log(reportParams.value);
});

</script>